@import "tailwindcss";

.scrollbar-thin {
  scrollbar-width: thin;
}
.scrollbar-thumb-blue-400 {
  scrollbar-color: #60a5fa #e0f2fe;
}

.animate-pulse {
  animation: pulse 1.5s cubic-bezier(.4,0,.6,1) infinite;
}
@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: .4; }
}
